.registerScreen {
  max-width: 300px;
  padding: 70px;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0, 0, 0, 0.85);
}
.register {
  position: relative;
  height: 100%;
  background: url("../img/netflix_background.png") center no-repeat;
  background-size: cover;
}
.registerScreen > form {
  display: grid;
  flex-direction: column;
}

.registerScreen > form > h1 {
  text-align: left;
  margin-bottom: 25px;
}

.registerScreen > form > h3 {
  font-size: 1.3rem;
  font-weight: 400;
}

.registerScreen > form > input {
  outline-width: 0;
  height: 40px;
  margin-bottom: 14px;
  border-radius: 5px;
  border: none;
  padding: 5px 15px;
}

.registerScreen > form > button {
  padding: 16px 20px;
  font-size: 1rem;
  color: #fff;
  border-radius: 5px;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}

.registerScreen_logo {
  position: fixed;
  left: 0px;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
  padding-top: 20px;
}

.registerScreen_button {
  position: fixed;
  right: 20px;
  top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #e50914;
  font-weight: 600;
  border: none;
  cursor: pointer;
}

.registerScreen_gradient {
  width: 100%;
  z-index: 1;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}

.registerScreen_body {
  position: absolute;
  top: 20%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  z-index: 1;
  color: #fff;
  padding: 20px;
  left: 0;
  right: 0;
}

.registerScreen_body > h1 {
  font-size: 3.125rem;
  margin-bottom: 20px;
}

.registerScreen_body > h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 30px;
}

.registerScreen_body > h3 {
  font-size: 1.3rem;
  font-weight: 400;
}
